import React from "react";
import adminLogin from "./admin/views/Login/index";
import adminHome from "./admin/views/Landing";
import adminUser from "./admin/views/User/index";
import adminLocation from "./admin/views/Location/index.jsx";
import adminUserRecommendLocation from "./admin/views/UserAddedLocation";
import RecommendedPosition from './admin/views/RecommendedPosition.js'
import { BrowserRouter as Router, Route } from "react-router-dom";
import "./admin/assets/style/media.css";
import home from "./client/views/home/index";
import { UserProvider } from "./admin/AdminContext";
// import login from "./auth/Login";
function App() {
  const [sideBarState, setSideBarState] = React.useState("check");
  return (
    <Router>
      <UserProvider value={sideBarState}>
        <Route path="/admin/home" component={adminHome} />

        <Route path="/admin/user" component={adminUser} />
        <Route path="/admin/location" component={adminLocation} />
        <Route
          path="/admin/user_recommended_location"
          component={adminUserRecommendLocation}
        />
        <Route
          path="/admin/user_recommended_location_approved"
          component={adminUserRecommendLocation}
        /> <Route
          path="/admin/user_recommended_location_ignored"
          component={adminUserRecommendLocation}
        />
        <Route
          path="/admin/user_recommended_position"
          component={RecommendedPosition}
        />
        <Route
          path="/admin/user_recommended_position_ignored"
          component={RecommendedPosition}
        />  <Route
          path="/admin/user_recommended_position_approved"
          component={RecommendedPosition}
        />
        <Route path="/" exact component={adminLogin} />
      </UserProvider>
    </Router>
  );
}

export default App;
