import React from "react";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepButton from "@material-ui/core/StepButton";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { MuiThemeProvider } from "@material-ui/core/styles";

import styles from "./styles";
// import getMuiTheme from "@material-ui/core/getMuiTheme";

const muiTheme = withStyles({
  stepper: {
    iconColor: "green", // or logic to change color
  },
});
function getSteps() {
  return;
}

function HorizontalNonLinearAlternativeLabelStepper(props) {
  const { classes } = props;
  React.useEffect(() => {
    console.log(props.stepper)
  }, [])

  const [activeStep, setActiveStep] = React.useState(0);
  const steps = props.stepper

  const handleStep = (step) => () => {
    setActiveStep(step);
    props.step(step);
  };

  return (
    <div className={classes.root}>
      <MuiThemeProvider muiTheme={muiTheme}>
        <Stepper alternativeLabel nonLinear activeStep={activeStep}>
          {steps.map((label, index) => {
            const stepProps = {};
            const buttonProps = {};

            return (
              <Step key={label} {...stepProps}>
                <StepButton onClick={handleStep(index)} {...buttonProps}>
                  {label}
                </StepButton>
              </Step>
            );
          })}
        </Stepper>
      </MuiThemeProvider>
    </div>
  );
}

HorizontalNonLinearAlternativeLabelStepper.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(
  HorizontalNonLinearAlternativeLabelStepper
);
