import React, { useContext } from "react";
import SideNavPage from "../../component/Sidebar/index";
import MaterialTable from "material-table";
import FadeIn from "react-fade-in";
import axios from "axios";
import { ip } from "../../config";
import {
  Grid,
  Container,
  Zoom,
  Slide,
  Grow,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Input,
} from "@material-ui/core";
import UserContext from "../../AdminContext";

import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core/styles";
import { withStyles } from "@material-ui/core/styles";
import EditIcon from "@material-ui/icons/Edit";
import UserImage from "../../assets/img/profile-bg.jpg";
import UserImage1 from "../../assets/img/bg.jpg";
import PhotoCameraIcon from "@material-ui/icons/PhotoCamera";
import DeleteIcon from "@material-ui/icons/Delete";
import CloseIcon from "@material-ui/icons/Close";
import DoneIcon from "@material-ui/icons/Done";
import { makeStyles, useTheme } from "@material-ui/core/styles";

import CircularProgress from "@material-ui/core/CircularProgress";

import PropTypes from "prop-types";
import { color } from "../../assets/style/styles";
// import styles from "./Styles";
import styles from "./styles";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});
const themeTable = createMuiTheme({
  overrides: {
    MuiTableRow: {
      root: {
        "&:hover": {
          backgroundColor: "#f2f2f2",
        },
        // borderTop: "1px solid lightgrey",
      },
    },
    MuiTypography: {
      h6: {
        fontSize: "190%",
        fontWeight: "bolder !important",
        color: color.theme,
      },
    },
  },
});

function User(props) {
  const { classes } = props;
  const { sideBar, setSidebar } = useContext(UserContext);

  const [openSideBar, setOpenSideBar] = React.useState(sideBar);
  const [marginLeft, setMarginLeft] = React.useState("220px");

  const [open, setOpen] = React.useState(false);

  const [userModal, setUserModal] = React.useState({
    image: null,
  });

  const [newImage, setNewImage] = React.useState(null);
  const [Name, setName] = React.useState("");
  const [Address, setAddress] = React.useState("");
  const [Phone, setPhone] = React.useState("");
  const [loading, setLoading] = React.useState(false);
  const [updatedData, setDataUpdated] = React.useState(false);
  const [userData, setUserData] = React.useState([]);
  const [userSelectedId, setUserSelectedId] = React.useState();
  const [state, setState] = React.useState({
    columns: [
      {
        title: "Id",
        editable: "never",
        render: (rowData) => rowData.tableData.id + 1,
      },

      {
        title: "Email",
        field: "email",
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
      {
        title: "Name",
        field: "full_name",
        cellStyle: {},
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
      {
        title: "Gender",
        field: "gender",
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
      {
        title: "City",
        field: "city",
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
    ],
    data: [],
  });
  React.useEffect(() => {
    getData();
  }, []);
  const setOpenCloseSideBar = (e) => {
    if (e === true) {
      setMarginLeft("220px");
    } else {
      setMarginLeft("50px");
    }
  };

  const handleClose = () => {
    setOpen(false);
    setNewImage(null);
  };
  const getData = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_IP}/userAuth/create_user/`,
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

        "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
      },
    }).then((res) => {
      setUserData(res.data);
    });
  };

  const Delete = (e, data) => {
    axios({
      method: "DELETE",
      url: `${process.env.REACT_APP_IP}/userAuth/manage_user/${data.id}/`,
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

        "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
        // Authorization: localStorage.getItem("token"),
      },
    })
      .then((data) => {
        getData();
        setLoading(false);
        setOpen(false);
        setDataUpdated(true);
      })
      .catch(function (response) {
        //handle error
      });
  };

  const SubmitData = (e) => {
    let form_data = new FormData();
    form_data.append("name", Name);

    form_data.append("phone", Phone);

    form_data.append("address", Address);

    if (userModal.image === null && newImage !== null) {
      form_data.append("image", newImage);
    } else if (userModal.image !== null && newImage === null) {
      form_data.append("image", userModal.image);
    } else {
      form_data.append("image", null);
    }
    setLoading(true);

    axios({
      method: "PUT",
      url: `${process.env.REACT_APP_IP}/userAuth/manage_user/${userSelectedId}/`,
      data: form_data,
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

        "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
        // Authorization: localStorage.getItem("token"),
      },
    })
      .then((data) => {
        getData();
        setLoading(false);
        setOpen(false);
        setDataUpdated(true);
      })
      .catch(function (response) {
        //handle error
        console.log(response);
      });
  };
  return (
    <div className={classes.backColor}>
      <div>
        <SideNavPage open={(e) => setOpenCloseSideBar(e)} />
      </div>
      <div className={classes.divHeight} style={{ marginLeft: marginLeft }}>
        <FadeIn transitionDuration="2000">
          <Grid
            direction="row"
            item
            container
            alignItems="center"
            alignContent="center"
          >
            <Grid direction="column" xl={1} lg={1} md={1} sm={1}></Grid>
            <Grid direction="column" xl={10} lg={10} md={10} sm={10}>
              {" "}
              {/* <FadeIn setTimeout={1000}> */}
              <Grow
                in={true}
                style={{ transformOrigin: "0 0 0" }}
                {...(true ? { timeout: 1000 } : {})}
              >
                <ThemeProvider theme={themeTable}>
                  <MaterialTable
                    title="Users"
                    columns={state.columns}
                    data={userData}
                    options={{
                      pageSize: 20,
                      pageSizeOptions: [5, 10, 20, 30, 50, 75, 100],
                      toolbar: true,
                      paging: true,
                      actionsColumnIndex: -1,
                      rowStyle: (x) => {
                        if (x.tableData.id % 2) {
                          return { backgroundColor: "#f2f2f2" };
                        }
                      },
                      headerStyle: {
                        color: "rgb(39, 101, 114)",
                        fontSize: "120%",
                        fontWeight: "bold",
                      },
                      // tableRow: {
                      //   hover: {
                      //     color: "green",
                      //   },
                      // },
                    }}
                    actions={[
                      // First action
                      (rowData) => ({
                        icon: () => <DeleteIcon />,
                        tooltip: "Delete",
                        onClick: (event, rowData) => Delete(event, rowData),
                      }),
                    ]}
                  />
                </ThemeProvider>
              </Grow>
            </Grid>{" "}
            <Grid direction="column" xl={1} lg={1} md={1} sm={1}></Grid>
          </Grid>
        </FadeIn>
      </div>
    </div>
  );
}

User.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(User);
