import React, { useContext } from "react";
import { color } from "../../assets/style/styles";
import axios from "axios";
import {
  textField,
  Item,
  Heading,
  PicSubHeading,
  SignIn,
  SignInHeading,
  Link,
} from "./Styles";
// import "../../assets/style/style.css";
import { Redirect } from "react-router-dom";
import {
  Grid,
  InputAdornment,
  FormControl,
  InputLabel,
  Input,
  IconButton,
  Button,
} from "@material-ui/core";
import UserContext from "../../AdminContext";

import FadeIn from "react-fade-in";
import { Email, Visibility, VisibilityOff } from "@material-ui/icons";

const height = window.screen.height;

function Login() {
  const {
    sideBar,
    setSidebar,
    logged_in,
    setLoggedIn,
    updateState,
  } = useContext(UserContext);

  const [values, setValues] = React.useState({
    email: "",
    password: "",
    showPassword: false,
  });
  const [updateUser, setUpdate] = React.useState(updateState);

  const [loggedIn, setLogged_In] = React.useState(false);
  const [errorCheck, setError] = React.useState(false);

  React.useEffect(() => {
    var update = updateUser;
    if (logged_in) {
      setLogged_In(true);
    } else {
      setLogged_In(false);
    }
  });
  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  //data Submission

  const submitLogin = (e) => {
    e.preventDefault();
    const payload = {
      password: values.password,
      email: values.email,
    };
    axios
      .post(`${process.env.REACT_APP_IP}/api/token/`, payload)
      .then((response) => {
      
        localStorage.setItem("token", response.data.access);
        localStorage.setItem("reftoken", response.data.refresh);
        setLogged_In(true);
        setLoggedIn(true);
        // dispatch(authSuccess(response.data.access, response.data.refresh));
      })
      .catch((err) => {
        setError(true);
        // dispatch(authFail());
      });

    axios
      .post(`${process.env.REACT_APP_IP}/user/token/`, payload)
      .then((response) => {

        // dispatch(authSuccess(response.data.access, response.data.refresh));
      })
      .catch((err) => {
        console.log(err);
        setError(true);
      });

   
  };
  return (
    <FadeIn transitionDuration="1000" childClassName="childFade">
      {loggedIn ? <Redirect to="/admin/home" /> : null}
      <Grid
        style={{ overflow: "hidden" }}
        item
        container
        direction="row"
        alignContent="center"
      >
      

        <Grid
          className="childFade"
          direction="column"
          xl={12}
          lg={12}
          md={12}
          sm={12}
          xs={12}
        >
          <SignIn>
            <Grid item direction="row">
              <SignInHeading>Sign In</SignInHeading>
            </Grid>
            <form onSubmit={submitLogin}>
              <Grid item direction="row">
                {" "}
                <FormControl>
                  <textField>
                    <InputLabel
                      error={errorCheck}
                      htmlFor="standard-adornment-password"
                    >
                      Email
                    </InputLabel>
                    <Input
                      error={errorCheck}
                      id="standard-adornment-password"
                      type="email"
                      value={values.email}
                      required
                      onChange={handleChange("email")}
                      endAdornment={
                        <InputAdornment position="end">
                          <IconButton aria-label="toggle password visibility">
                            <Email
                              style={{
                                color: errorCheck ? color.danger : color.theme,
                              }}
                            />
                          </IconButton>
                        </InputAdornment>
                      }
                    />
                  </textField>
                </FormControl>
              </Grid>

              <Grid item direction="row" style={{ marginTop: "20px" }}>
                <textField>
                  <FormControl>
                    <InputLabel
                      error={errorCheck}
                      htmlFor="standard-adornment-password"
                    >
                      Password
                    </InputLabel>
                    <Input
                      error={errorCheck}
                      id="standard-adornment-password"
                      required
                      type={values.showPassword ? "text" : "password"}
                      value={values.password}
                      onChange={handleChange("password")}
                      endAdornment={
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleClickShowPassword}
                            onMouseDown={handleMouseDownPassword}
                          >
                            {values.showPassword ? (
                              <Visibility
                                style={{
                                  color: errorCheck
                                    ? color.danger
                                    : color.theme,
                                }}
                              />
                            ) : (
                              <VisibilityOff
                                style={{
                                  color: errorCheck
                                    ? color.danger
                                    : color.theme,
                                }}
                              />
                            )}
                          </IconButton>
                        </InputAdornment>
                      }
                    />
                  </FormControl>
                </textField>
              </Grid>
              <Grid item direction="row" style={{ marginTop: "50px" }}>
                <Button
                  type="submit"
                  variant="contained"
                  style={{ backgroundColor: color.theme, color: "white" }}
                >
                  {" "}
                  Sign In
                </Button>
              </Grid>
              <Grid item direction="row" style={{ marginTop: "20px" }}>
                <Link>Forget your password?</Link>
              </Grid>
            </form>
          </SignIn>
        </Grid>
      </Grid>
    </FadeIn>
  );
}

export default Login;
