import React, { forwardRef, useContext } from "react";
import SideNavPage from "../../component/Sidebar/index";
import MaterialTable from "material-table";
import FadeIn from "react-fade-in";
import { Grow, Grid, Drawer, IconButton } from "@material-ui/core";
import clsx from "clsx";
import UserContext from "../../AdminContext";
import TextTruncate from "react-text-truncate";
import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core/styles";
// import { ip } from "../../config";
import {
  Button, 
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Slide,
} from "@material-ui/core";
import axios from "axios";
import { withStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import styles from "./Styles";

import Fab from "@material-ui/core/Fab";
import EditRoundedIcon from '@material-ui/icons/EditRounded';

import { color } from "../../assets/style/styles";
import Steeper from "../../component/Steeper/index";

const themeTable = createMuiTheme({
  overrides: {
    MuiTableRow: {
      root: {
        "&:hover": {
          backgroundColor: "#f2f2f2",
        },
        // borderTop: "1px solid lightgrey",
      },
    },
    MuiTypography: {
      h6: {
        fontSize: "190%",
        fontWeight: "bolder !important",
        color: color.theme,
      },
    },
  },
});

const StyledButton = withStyles({
  root: {
    background: color.theme,
  },
})(Fab);
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});
function Landing(props) {
  const { sideBar, setSidebar } = useContext(UserContext);
  const AnyReactComponent = ({ text }) => <div>{text}</div>;

  const [open, setOpen] = React.useState(false);
  const [marginLeft, setMarginLeft] = React.useState("220px");
  const [center, setCenter] = React.useState({
    lat: 59.95,
    lng: 30.33,
  });
  const [title, setTitle] = React.useState("");
  const [message, setMessage] = React.useState("");
  const [stepperList, setStepperList] = React.useState([]);
  const [allData, setAllData] = React.useState([])
  const { classes } = props;
  const [editTable, setEditTable] = React.useState(window.location.pathname !== "/admin/user_recommended_location_approved" ? {

    onRowUpdate: (newData, oldData) =>
      new Promise((resolve) => {
        setTimeout(() => {
          resolve();
          if (oldData) {
            setState((prevState) => {
              const data = [...prevState.data];
              updateData(data.indexOf(oldData), newData);

              data[data.indexOf(oldData)] = newData;

              return { ...prevState, data };
            });
          }
        }, 600);
      }),

  }
    : null)
  const [state, setState] = React.useState({
    columns: [
      {
        title: "Id",
        editable: "never",
        render: (rowData) => rowData.tableData.id + 1,
      },

      {
        title: "Email",
        field: "user__email",
        cellStyle: {},
        editable: "never",

        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },

      {
        title: "Message",
        field: "message",
        editable: "never",

        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
        cellStyle: {
          width: 500,
          minWidth: 500,
        },
        render: (rowData) => (
          <p>
            <TextTruncate
              line={1}
              element="span"
              truncateText="…"
              text={rowData.message}
              textTruncateChild={
                <a onClick={(e) => openMessage(rowData)} href="#">
                  Read more
                </a>
              }
            />

            {/* <IconButton>
              <RoomIcon
                style={{ color: "black" }}
                onClick={(e) => openMap(rowData)}
              />
            </IconButton> */}

          </p>
        ),
      },
      {
        title: "Status",
        field: "status",
        lookup: { 1: "pending", 2: "ignore", 3: "approved" },
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
    ],
    data: [

    ],
  });
  const [data, setData] = React.useState();
  React.useEffect(() => {
    // AddNewData();
    getData();
  }, []);

  const getData = () => {
    var status = 1
    if (window.location.pathname == "/admin/user_recommended_location_approved") {
      status = 3
    }
    else if (window.location.pathname == "/admin/user_recommended_location_ignored") {
      status = 2
    }
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_IP}/adminuser/recommended_location/${status}/`,

      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

        "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        setAllData(res.data.data)
        setStepperList(res.data.gener)

        if (title === "") {
          setData(res.data.data.filter(i => i.gener === res.data.gener[0]));
          setTitle(res.data.gener[0])
        }
        else {
          setData(res.data.data.filter(i => i.gener === title));
        }

        // setState({ data: res.data });
      })
      .catch(function (response) {
        //handle error
      });
  };

  const AddNewData = () => {
    const data = {
      email: "",
      message: "",
      status: 1,
    };

    axios({
      method: "POST",
      url: `${process.env.REACT_APP_IP}/adminuser/recommended_location/`,
      data: data,
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

        "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
        // Authorization: localStorage.getItem("token"),
      },
    })
      .then((data) => {
        getData();
      })
      .catch(function (response) {
        //handle error
        console.log(response);
      });
  };

  const openMessage = (data) => {
    setTitle(data.email);
    setMessage(data.message);
    setOpen(true);
  };
  const setOpenCloseSideBar = (e) => {
    if (e === true) {
      setMarginLeft("220px");
    } else {
      setMarginLeft("50px");
    }
  };

  const handleClose = () => {
    setOpen(false);
  };
  const updateData = (oldDataIndex, newData) => {
    // old index will show the old index of the data

    const datanew = {
      email: newData.email,
      message: newData.message,

      status: newData.status,
    };

    axios({
      method: "PUT",
      url: `${process.env.REACT_APP_IP}/adminuser/manage_recommended_location/${newData.id}/`,
      data: JSON.stringify(datanew),
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
      },
    })
      .then((data) => {
        getData();
      })
      .catch(function (response) { });
  };
  const stepperSet = e => {
    for (var i = 0; i < stepperList.length; i++) {
      if (e == i) {
        setTitle(stepperList[i])
        setData(allData.filter(j => j.gener === stepperList[i]))

      }
    }

  }
  return (
    <div className={classes.backColor}>
      <Dialog
        fullWidth
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{title}</DialogTitle>
        <DialogContent>{message}</DialogContent>
        <DialogActions>
          <Grid
            direction="row"
            item
            container
            alignItems="center"
            alignContent="center"
            className={classes.textalignment}
            style={{ marginTop: "20px" }}
          >
            <Grid direction="column" xl={12} lg={12} md={12} sm={12} xs={12}>
              <Button
                onClick={handleClose}
                variant="contained"
                className={classes.modalButton}
                component="span"
              >
                Close
              </Button>
            </Grid>
          </Grid>
        </DialogActions>
      </Dialog>

      <div>
        <SideNavPage open={(e) => setOpenCloseSideBar(e)} />
      </div>
      <div className={classes.divHeight} style={{ marginLeft: marginLeft }}>
        <Steeper step={(e) => stepperSet(e)} stepper={stepperList} />

        <FadeIn transitionDuration="2000">
          <Grid
            direction="row"
            item
            container
            alignItems="center"
            alignContent="center"
          >
            <Grid direction="column" xl={1} lg={1} md={1} sm={1}></Grid>
            <Grid direction="column" xl={10} lg={10} md={10} sm={10}>
              <Grow
                in={true}
                style={{ transformOrigin: "0 0 0" }}
                {...(true ? { timeout: 1000 } : {})}
              >
                <ThemeProvider theme={themeTable}>
                  <MaterialTable
                    localization={{
                      body: {
                        editRow: {
                          saveTooltip: "update",
                          cancelTooltip: "Cancel",
                        },

                        editTooltip: "update status",
                      },
                    }}
                    icons={{
                      Edit: forwardRef((props, ref) => (
                        <div style={{}}>
                          <StyledButton
                            size="small"
                            ref={ref}
                            {...props}
                            color="primary"
                            aria-label="add"
                          >
                            <EditRoundedIcon />
                          </StyledButton>
                        </div>
                      )),
                    }}
                    title={title}
                    columns={state.columns}
                    data={data}
                    options={{
                      pageSize: 20,
                      pageSizeOptions: [5, 10, 20, 30, 50, 75, 100],
                      toolbar: true,
                      paging: true,
                      actionsColumnIndex: -1,
                      rowStyle: (x) => {
                        if (x.tableData.id % 2) {
                          return { backgroundColor: "#f2f2f2" };
                        }
                      },
                      headerStyle: {
                        color: "rgb(39, 101, 114)",
                        fontSize: "120%",
                        fontWeight: "bold",
                      },
                    }}
                    editable={editTable}
                  />
                </ThemeProvider>
              </Grow>
            </Grid>
            <Grid direction="column" xl={1} lg={1} md={1} sm={1}></Grid>
          </Grid>
        </FadeIn>
      </div>
    </div>
  );
}

Landing.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(Landing);
