import React, { useEffect, useContext } from "react";
import { Redirect } from "react-router-dom";
import { Grid } from "@material-ui/core";
import FiberManualRecordIcon from "@material-ui/icons/FiberManualRecord";
import clsx from "clsx";
import { useTheme } from "@material-ui/core/styles";
// import { ip } from "../../config";
import UserContext from "../../AdminContext";
import { color } from "../../assets/style/styles";

import {
  Drawer,
  CssBaseline,
  AppBar,
  Toolbar,
  List,
  IconButton,
  ListItem,
  ListItemIcon,
  ListItemText,
  Avatar,
  Menu,
  MenuItem,
  Button,
} from "@material-ui/core";

import MenuIcon from "@material-ui/icons/Menu";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import HomeOutlinedIcon from "@material-ui/icons/HomeOutlined";
import ClearRoundedIcon from "@material-ui/icons/ClearRounded";
// import AppLogo from "../../assets/img/logo.jpg";
import AppLogo from "../../assets/img/logo1.png";

import AppLogo1 from "../../assets/img/logo.PNG";

import PeopleOutlineIcon from "@material-ui/icons/PeopleOutline";
import AddLocationIcon from "@material-ui/icons/AddLocation";
import QueueIcon from "@material-ui/icons/Queue";
import PersonPinCircleIcon from '@material-ui/icons/PersonPinCircle';
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";

import styles from "./styles";
import Collapse from '@material-ui/core/Collapse';

import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));
function Main(props) {
  const { classes } = props;

  const {
    sideBar,
    setSidebar,
    logged_in,
    setLoggedIn,
    updateState,
  } = useContext(UserContext);

  const theme = useTheme();
  const [update, setUpdateState] = React.useState(updateState);
  const [open, setOpen] = React.useState(sideBar);
  const [signOut, setSignOUt] = React.useState(!logged_in);

  const [path, setPath] = React.useState("");
  const [anchorEl, setAnchorEl] = React.useState(false);
  const openside = Boolean(anchorEl);

  const Signout = () => {
    setAnchorEl(null);
    setSignOUt(true);
    localStorage.removeItem("token");
    localStorage.removeItem("reftoken");

    setLoggedIn(false);
  };
  React.useEffect(() => {
    props.open(sideBar);



    // var state = update;
    fetch(`${process.env.REACT_APP_IP}/adminuser/hello/`, {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    })
      .then((res) => res.json())
      .then((json) => {
        if (json.code === "token_not_valid") {
          setSignOUt(true);
          localStorage.removeItem("token");
          localStorage.removeItem("reftoken");

          setLoggedIn(false);
        }

        // this.setState({ username: json.username });
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleDrawerOpen = () => {
    setOpen(true);
    const sideBar = true;
    setSidebar(sideBar);
    props.open(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
    const sideBar = false;

    setSidebar(sideBar);
    props.open(false);
  };
  const clickHandler = (path) => {
    setPath(path);

  };
  const [openRecommend, setOpenRecommend] = React.useState(window.location.pathname === "/admin/user_recommended_position_approved" ? true : window.location.pathname === "/admin/user_recommended_position_ignored" ? true : window.location.pathname === "/admin/user_recommended_position" ? true : false);

  const handleClickRecommend = () => {
    setOpenRecommend(!openRecommend);
    setPath("/admin/user_recommended_position");
    // clickHandler(
  };
  const [openRecommendLocation, setOpenRecommendLocation] = React.useState(
    window.location.pathname === "/admin/user_recommended_location_ignored" ?
      true :
      window.location.pathname === "/admin/user_recommended_location_approved" ?
        true :
        window.location.pathname === "/admin/user_recommended_location" ?
          true : false);

  const handleClickFeedback = () => {
    setOpenRecommendLocation(!openRecommendLocation);
    setPath("/admin/user_recommended_location");
    // clickHandler(
  };
  return (
    <div className={classes.root}>
      {signOut ? <Redirect to="/" /> : null}
      {path !== "" && window.location.pathname !== path ? (
        <Redirect to={path} />
      ) : null}
      <CssBaseline />
      <AppBar
        style={{ backgroundColor: "white" }}
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          {!open ? (
            <IconButton
              onClick={(e) => handleDrawerOpen()}
              style={{ marginLeft: "40px" }}
            >
              <MenuIcon style={{ color: "inherit" }} />
            </IconButton>
          ) : null}{" "}
          <div className={classes.toolbarButtons}>
            <img src={AppLogo1} width="50px" height="30px" />
          </div>
          {/* <img src={AppLogo1} /> */}
        </Toolbar>
      </AppBar>

      {!open ? (
        <>
          <Drawer
            className={classes.drawersmall}
            variant="persistent"
            anchor="left"
            open={true}
            classes={{
              paper: classes.drawerPapersmall,
            }}
            style={{
              overflowX: "hidden",
            }}
          >
            <Grid
              item
              direction="column"
              container
              contentAlign="center"
              alignItems="center"
              className={classes.textalignment}
            >
              <Avatar
                onClick={handleMenu}
                alt="Remy Sharp"
                src={AppLogo}
                style={{ marginTop: "13px" }}
                className={classes.small}
              />{" "}
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={openside}
                onClose={handleClose}
              >
                <MenuItem onClick={Signout}>Sign Out</MenuItem>
              </Menu>
            </Grid>

            <List className={classes.listStyle}>
              <ListItem button href="#">
                <span className={classes.mainHeadStyle}>Main</span>
              </ListItem>
            </List>

            <IconButton
              onClick={(e) => {
                clickHandler("/admin/home");
              }}
            >
              <HomeOutlinedIcon
                style={{
                  color:
                    window.location.pathname === "/admin/home"
                      ? color.theme
                      : "grey",
                }}
              />
            </IconButton>

            <IconButton
              onClick={(e) => {
                clickHandler("/admin/location");
              }}
            >
              <AddLocationIcon
                style={{
                  color:
                    window.location.pathname === "/admin/location"
                      ? color.theme
                      : "grey",
                }}
              />
            </IconButton>
            <IconButton
              onClick={(e) => {
                clickHandler("/admin/user");
              }}
            >
              <PeopleOutlineIcon
                style={{
                  color:
                    window.location.pathname === "/admin/user"
                      ? color.theme
                      : `grey`,
                }}
              />
            </IconButton>
            <IconButton
              onClick={(e) => {
                clickHandler("/admin/user_recommended_location");
              }}
            >
              <QueueIcon
                style={{
                  color:
                    window.location.pathname ===
                      "/admin/user_recommended_location"
                      ? color.theme
                      : "grey",
                }}
              />
            </IconButton>
            <IconButton
              onClick={(e) => {
                clickHandler("/admin/user_recommended_position");
              }}
            >
              <PersonPinCircleIcon
                style={{
                  color:
                    window.location.pathname ===
                      "/admin/user_recommended_position"
                      ? color.theme
                      : "grey",
                }}
              />
            </IconButton>
          </Drawer>
        </>
      ) : null}
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={open}
        classes={{
          paper: classes.drawerPaper,
        }}
        style={{}}
      >
        <div className={classes.drawerHeader}>
          <Grid
            item
            direction="column"
            container
            contentAlign="left"
            alignItems="left"
            style={{ textAlign: "left" }}
          >
            <Button className={classes.AvatarButton} onClick={handleMenu}>
              <Avatar
                alt="Remy Sharp"
                src={AppLogo}
                className={classes.small}
              />{" "}
              <span className={classes.SideAdminHead}> Admin</span>
              <ArrowDropDownIcon className={classes.SideAdminHead} />
            </Button>
            <Menu
              id="menu-appbar"
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={openside}
              onClose={handleClose}
            >
              <MenuItem onClick={Signout}>Sign Out</MenuItem>
            </Menu>
          </Grid>

          <IconButton onClick={handleDrawerClose}>
            {theme.direction === "ltr" ? (
              <ClearRoundedIcon style={{ color: "white" }} />
            ) : (
                <ClearRoundedIcon style={{ color: "white" }} />
              )}
          </IconButton>
        </div>

        <List style={{ color: "white", backgroundColor: color.sideBarMain }}>
          <ListItem>
            <span style={{ fontSize: "90%", color: "grey" }}>Main</span>
          </ListItem>
        </List>
        <List className={classes.list}>
          <ListItem
            button
            href="#"
            onClick={(e) => {
              clickHandler("/admin/home");
            }}
            className={
              window.location.pathname === "/admin/home"
                ? `${classes.active}`
                : `${classes.notActive}`
            }
          >
            <ListItemIcon></ListItemIcon>
            <ListItemText
              classes={{ primary: classes.listItemText }}
              primary="Home"
            />
            <span style={{ fontSize: "10px" }}>
              {window.location.pathname === "/admin/home" ? (
                <FiberManualRecordIcon fontSize="inherit" />
              ) : null}
            </span>
          </ListItem>
        </List>
        <List className={classes.list}>
          <ListItem
            button
            href="#"
            onClick={(e) => {
              clickHandler("/admin/location");
            }}
            className={
              window.location.pathname === "/admin/location"
                ? `${classes.active}`
                : `${classes.notActive}`
            }
          >
            <ListItemIcon></ListItemIcon>
            <ListItemText
              classes={{ primary: classes.listItemText }}
              primary="Locations"
            />
            <span className={classes.listItemTextFontSize}>
              {window.location.pathname === "/admin/location" ? (
                <FiberManualRecordIcon fontSize="inherit" />
              ) : null}
            </span>
          </ListItem>
        </List>

        <List className={classes.list}>
          <ListItem
            button
            href="#"
            onClick={(e) => {
              clickHandler("/admin/user");
            }}
            className={
              window.location.pathname === "/admin/user"
                ? `${classes.active}`
                : `${classes.notActive}`
            }
          >
            <ListItemIcon></ListItemIcon>
            <ListItemText
              classes={{ primary: classes.listItemText }}
              primary="User"
            />
            <span className={classes.listItemTextFontSize}>
              {window.location.pathname === "/admin/user" ? (
                <FiberManualRecordIcon fontSize="inherit" />
              ) : null}
            </span>
          </ListItem>
        </List>
        <List className={classes.list}>
          <ListItem
            button
            href="#"
            onClick={(e) => {
              // clickHandler("/admin/user_recommended_location");
              handleClickFeedback()
            }}
            className={
              window.location.pathname === "/admin/user_recommended_location"
                ? `${classes.active}`
                : `${classes.notActive}`
            }
          >
            <ListItemIcon></ListItemIcon>
            <ListItemText
              classes={{ primary: classes.listItemText }}
              primary="Recommend"
            />
            <span className={classes.listItemTextFontSize}>
              {
                openRecommendLocation ? <ExpandLess fontSize="5px" /> : <ExpandMore fontSize="5px" />
              }
            </span>
          </ListItem>
        </List>
        <Collapse in={openRecommendLocation} timeout="auto" unmountOnExit>
          <List className={classes.listCollapse}>
            <ListItem
              button
              href="#"
              onClick={(e) => {
                clickHandler("/admin/user_recommended_location_approved");
              }}
              className={
                window.location.pathname === "/admin/user_recommended_location_approved"
                  ? `${classes.active}`
                  : `${classes.notActive}`
              }
            >
              <ListItemIcon></ListItemIcon>
              <ListItemText
                classes={{ primary: classes.listItemTextCollapse }}
                primary="Approved"
              />
              <span className={classes.listItemTextFontSize}>
                {window.location.pathname ===
                  "/admin/user_recommended_location_approved" ? (
                    <FiberManualRecordIcon fontSize="inherit" />
                  ) : null}
              </span>
            </ListItem>

          </List>
          <List className={classes.listCollapse}>
            <ListItem
              button
              href="#"
              onClick={(e) => {
                clickHandler("/admin/user_recommended_location_ignored");
              }}
              className={
                window.location.pathname === "/admin/user_recommended_location_ignored"
                  ? `${classes.active}`
                  : `${classes.notActive}`
              }
            >
              <ListItemIcon></ListItemIcon>
              <ListItemText
                classes={{ primary: classes.listItemTextCollapse }}
                primary="Ignored"
              />
              <span className={classes.listItemTextFontSize}>
                {window.location.pathname ===
                  "/admin/user_recommended_location_ignored" ? (
                    <FiberManualRecordIcon fontSize="inherit" />
                  ) : null}
              </span>
            </ListItem>

          </List>

        </Collapse>


        <List className={classes.list}>
          <ListItem
            button
            href="#"
            onClick={(e) => {
              // clickHandler("/admin/user_recommended_position");
              handleClickRecommend()

            }}
            className={
              window.location.pathname === "/admin/user_recommended_position"
                ? `${classes.active}`
                : `${classes.notActive}`
            }
          >
            <ListItemIcon></ListItemIcon>
            <ListItemText
              classes={{ primary: classes.listItemText }}
              primary="Position"
            />
            <span className={classes.listItemTextFontSize}>
              {
                openRecommend ? <ExpandLess fontSize="5px" /> : <ExpandMore fontSize="5px" />
              }
            </span>
          </ListItem>

        </List>
        <Collapse in={openRecommend} timeout="auto" unmountOnExit>
          <List className={classes.listCollapse}>
            <ListItem
              button
              href="#"
              onClick={(e) => {
                clickHandler("/admin/user_recommended_position_ignored");
              }}
              className={
                window.location.pathname === "/admin/user_recommended_position_ignored"
                  ? `${classes.active}`
                  : `${classes.notActive}`
              }
            >
              <ListItemIcon></ListItemIcon>
              <ListItemText
                classes={{ primary: classes.listItemTextCollapse }}
                primary="Ignored"
              />
              <span className={classes.listItemTextFontSize}>
                {window.location.pathname ===
                  "/admin/user_recommended_position_ignored" ? (
                    <FiberManualRecordIcon fontSize="inherit" />
                  ) : null}
              </span>
            </ListItem>

          </List>
          <List className={classes.listCollapse}>
            <ListItem
              button
              href="#"
              onClick={(e) => {
                clickHandler("/admin/user_recommended_position_approved");
              }}
              className={
                window.location.pathname === "/admin/user_recommended_position_approved"
                  ? `${classes.active}`
                  : `${classes.notActive}`
              }
            >
              <ListItemIcon></ListItemIcon>
              <ListItemText
                classes={{ primary: classes.listItemTextCollapse }}
                primary="Approved"
              />
              <span className={classes.listItemTextFontSize}>
                {window.location.pathname ===
                  "/admin/user_recommended_position_approved" ? (
                    <FiberManualRecordIcon fontSize="inherit" />
                  ) : null}
              </span>
            </ListItem>

          </List>

        </Collapse>


      </Drawer>
    </div >
  );
}

Main.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(Main);
