import { color } from "../../assets/style/styles";
export default (theme) => ({
  backColor: {
    backgroundColor: color.bgColor,
  },

  modalButton: {
    backgroundColor: color.theme,
    color: "white",
    minWidth: "110px",
    maxWidth: "110px",
    marginRight: "10px",
    "&:hover": {
      backgroundColor: color.buttonHover,
    },
  },
  divHeight: {
    marginLeft: "65px",
    marginRight: "20px",
    marginTop: "100px",
  },

  root: {
    padding: "2px 4px",
    display: "flex",
    alignItems: "center",
    width: 400,
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
});
