const themeTable = createMuiTheme({
  overrides: {
    MuiTableRow: {
      root: {
        "&:hover": {
          backgroundColor: "#f2f2f2",
        },
      },
    },
    MuiTypography: {
      h6: {
        fontSize: "190%",
        fontWeight: "bolder !important",
        color: color.theme,
      },
    },
  },
});

const StyledButton = withStyles({
  root: {
    background: color.theme,
    "&:hover": {
      backgroundColor: "rgb(38, 100, 115)",
    },
  },
})(Fab);

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});
function Location(props) {
  const { classes } = props;
  const { sideBar, setSidebar } = useContext(UserContext);
 
  const [open, setOpen] = React.useState(false);
 
  const [currentCount, setCurrentCount] = React.useState(500);
  const [marginLeft, setMarginLeft] = React.useState("220px");

  const [title, setTitle] = React.useState("Resturant");
  const [backtitle, setBackTitle] = React.useState("Resturant");

  //pit stop is the list of all pit stop added ,
  //it should be in the form as it is representing
  //1 and 2 is the id of the pit stop and table
  const [pitstop1, setpit] = React.useState(null);

  const [searchText, setSearchText] = React.useState("");
  const [searchLoading, setSearchLoading] = React.useState(false);
  const [searchFromBackend, setSearchFromBackend] = React.useState(false);

  const state = [
    {
        title: "Id",
        field: "idk",
        cellStyle: {},
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
          textAlign: "left",
        },
        editable: "never",
        cellStyle: { textAlign: "left" },
      },
    // {
    //   title:"Id",
    //   render:(rowData)=>rowData.tableData.id+1
    // },

      {
        title: "Pit Stops",
        field: "pit_stop__name",
        // lookup: pitStops,
        cellStyle: {},
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
        editable: "never",
      },
      {
        title: "Name",
        field: "name",

        cellStyle: {},
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },

      {
        title: "Latitude",
        field: "latitude",
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
      {
        title: "Longitude",
        field: "longitude",
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
      {
        title: "City",
        field: "city",

        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
          textAlign: "left",
        },
        cellStyle: { textAlign: "center" },
      },
      {
        title: "Distance",
        field: "distance_stop",
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
      {
        title: "Rating",
        field: "rating_stop",
        editable: "never",

        type: "numeric",
        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
      },
      {
        title: "Map View",
        field: "map",
        editable: "never",

        headerStyle: {
          fontWeight: "bold",
          fontSize: "110%",
        },
        render: (rowData) => (
          <IconButton>
            <RoomIcon
              style={{ color: "black" }}
              onClick={(e) => openMap(rowData)}
            />
          </IconButton>
        ),
      },
    ]
 
  const [dataLocation, setDataLocation] = React.useState();
  const [pitStop, setPitStop] = React.useState();
  const [totalLocation, setTotalLocation] = React.useState();
  const [page, setPage] = React.useState(0);

  const [rowsPerPageShow, setRowsPerPageShow] = React.useState(20);
  const [slicedData, setSlicedData] = React.useState();
  
  const [allData, setAllData] = React.useState({})
  const [TotalCounts,setTotalCounts] = React.useState({})
 
  React.useEffect(() => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_IP}/adminuser/location/`,

      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

       
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
     
        setSlicedData(res.data.data.resturant.slice(0, 20));
        
        setTotalLocation(res.data.count.resturant)
        console.log('this is tesing pa',res.data.count)
        setAllData(res.data.data)

        setDataLocation(res.data.data.resturant);
        setCurrentCount(500)
        setTotalCounts(res.data.count)

      })
      .catch(function (response) {
        //handle error

        console.log(response);
      });

    return function cleanup() {};
  }, []);

  const locationList = async (
  
    count
   
  ) => {
    console.log(title)

    var countData = { start: count,location:'resturant' };
    setCurrentCount(count + 500);
   
   if(title==='Gas/Petrol Stations'){
    countData.location='gas_petrol'

   

    }
    else if(title === 'Bathrooms'){
      countData.location='bathroom'


    }
    axios({
      method: "POST",
      url: `${process.env.REACT_APP_IP}/adminuser/location_list/`,
      data: countData,

      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        // console.log(res.data);
        if(res.data.length > 0)
          {
            console.log('yes it is inserting the datas',res.data)
            setDataLocation([...dataLocation,...res.data])
            // setDataLocation.conca
          }
        console.log(dataLocation)
     
     
      })
      .catch(function (response) {
        //handle error

        console.log(response);
      });
  };
  const stepperSet = (e) => {
    setRowsPerPageShow(20);
    setPage(0);
    if (e === 0) {
      //get Resturant data
      setTitle("Resturant"); // getting the bathroom and gas petrol data
    //   setResturnatData(rest);
      setBackTitle("resturant");

      setDataLocation(allData.resturant);
      setTotalLocation(allData.resturant.length);

      setSlicedData(allData.resturant.slice(0, 20));
      // getData();
    } else if (e === 1) {
      //get Gas/Petrol Station data
      setTitle("Gas/Petrol Station");
      setBackTitle("gas_petrol");
      setTotalLocation(allData.gas_petrol.length);

    //   setResturnatData(gasPetrol);
      setDataLocation(allData.gas_petrol);
      setSlicedData(allData.gas_petrol.slice(0, 20));

      // getData("gas_petrol");
    } else if (e === 2) {
      //get Bathroom data
      setTitle("Bathroom");
      setBackTitle("bathroom");
      setTotalLocation(allData.bathroom.length);

      // setResturnatData(bath);
      setDataLocation(allData.bathroom);
      setSlicedData(allData.bathroom.slice(0, 20));

      // getData("bathroom");
    }
  };
  const openMap = (data) => {
    setpit(data);

    setOpen(true);
  };

  const handleClose = () => {
    setpit(null);
    setOpen(false);
  };
  const setOpenCloseSideBar = (e) => {
    if (e === true) {
      setMarginLeft("220px");
    } else {
      setMarginLeft("50px");
    }
  };
//   const AddNewData = (e) => {
//     var arr;
//     for (var key in pitStop) {
//       var pit = parseInt(e.pit_stop);
//       var pit1 = parseInt(pitStop[key].id);

//       if (pit === pit1) {
//         arr = pitStop[key];

//         break;
//       }
//     }
//     // console.log(arr);
//     var distance = getDistance(
//       {
//         latitude: parseFloat(arr.longitude),
//         longitude: parseFloat(arr.latitude),
//       },
//       { latitude: parseFloat(e.longitude), longitude: parseFloat(e.latitude) }
//     );
//     if (distance <= 3218.69) {
//       const data = {
//         pit_stop: e.pit_stop,
//         name: e.name,
//         longitude: e.longitude,
//         latitude: e.latitude,
//         rating_stop: 0,
//         distance_stop: distance / 1000,
//         location_type: backtitle,
//       };

//       axios({
//         method: "POST",
//         url: `${process.env.REACT_APP_IP}/adminuser/location/`,
//         data: JSON.stringify(data),
//         headers: {
//           Authorization: `Bearer ${localStorage.getItem("token")}`,

//           // "csrfmiddlewaretoken": csrfToken,
//           "Content-Type": "multipart/form-data",
//           "Content-Type": "application/json",
//           // Authorization: localStorage.getItem("token"),
//         },
//       })
//         .then((data) => {
//           getData();
//         })
//         .catch(function (response) {
//           //handle error
//         });
//     } else {
//       console.log(alert("please select location inside 2 miles"));
//     }

//     //add new Data here
//     //name:"1" this 1 is id of the pit stop add as it is
//   };
  const EditData = (e, data) => {
    var arr;
    for (var key in pitStop) {
      var pit = parseInt(e.pit_stop);
      var pit1 = parseInt(pitStop[key].id);

      if (pit === pit1) {
        arr = pitStop[key];

        break;
      }
    }

    // var distance = getDistance(
    //   {
    //     latitude: parseFloat(arr.longitude),
    //     longitude: parseFloat(arr.latitude),
    //   },
    //   {
    //     latitude: parseFloat(data.longitude),
    //     longitude: parseFloat(data.latitude),
    //   }
    // );

    // if (distance <= 3218.69) {
      const datanew = {
        // pit_stop: data.pit_stop,
        name: data.name,
        longitude: data.longitude,
        latitude: data.latitude,
        rating_stop: data.rating_stop,
        city:data.city
        // distance_stop: data.distance ,
      };

      axios({
        method: "PUT",
        url: `${process.env.REACT_APP_IP}/adminuser/manage_locations/${e.id}/`,
        data: JSON.stringify(datanew),
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
          // "Content-Type": "multipart/form-data",
          "Content-Type": "application/json",
        },
      })
        .then((data) => {
          getData();
        })
        .catch(function (response) {
          //handle error
          console.log(response);
        });
    // } else {
    //   console.log(alert("please select location inside 2 miles"));
    // }
  };
  const DeleteData = (e) => {
    axios({
      method: "DELETE",
      url: `${process.env.REACT_APP_IP}/adminuser/manage_locations/${e.id}/`,

      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

        // "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
      },
    })
      .then((data) => {
     
        
        dataLocation.splice(dataLocation.findIndex(p=>p.id == e.id),1)
        
        setSlicedData(
          dataLocation.slice(
            page * rowsPerPageShow,
            page * rowsPerPageShow + rowsPerPageShow
          )
        );
      })
      .catch(function (response) {
        //handle error
        console.log(response);
      });
  };

  const getData = () => {
    axios({
      method: "GET",
      url: `${process.env.REACT_APP_IP}/adminuser/location/`,

      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,

        // "Content-Type": "multipart/form-data",
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        var rest = [];
        console.log(res.data.data);
        var i = 1;
        // var bath = [];
        // var j = 1;
        // var gasPetrol = [];
        // var k = 1;
        var resData = res.data.data;
        var nbacktitle = backtitle.toLocaleLowerCase()
        resData[nbacktitle].forEach(e => {
          e.idk = i
          // res.data.data[e]["idk"] = i;
          rest.push(e);
          i++;
        });
       
        // locationList(rest, bath, gasPetrol, count);

        // setResturnatData(rest);
        setDataLocation(rest);
        setSlicedData(
          rest.slice(
            page * rowsPerPageShow,
            page * rowsPerPageShow + rowsPerPageShow
          )
        );
      })
      .catch(function (response) {
        //handle error

        console.log(response);
      });
  };

  const searchTheText = (e) => {
    e.preventDefault();
    setRowsPerPageShow(20);
    setPage(0);
    setSearchLoading(true);
    // console.log(searchText);
    var searchData = {
      text: searchText,
    };
    if(searchText!==''){
    
    axios({
      method: "POST",
      url: `${process.env.REACT_APP_IP}/adminuser/location_search_like/`,
      data: searchData,

      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        setSearchFromBackend(true);
        console.log(res.data)
        setSlicedData(res.data.resturant.slice(0, 20));
        setTotalLocation(res.data.resturant.length)
        setAllData(res.data)
        setDataLocation(res.data.resturant);
      
      })
      .catch(function (response) {
        //handle error      

        console.log(response);
      });
    }
    else{
      axios({
        method: "GET",
        url: `${process.env.REACT_APP_IP}/adminuser/location/`,
  
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
  
         
          "Content-Type": "application/json",
        },
      })
        .then((res) => {
       
          setSlicedData(res.data.data.resturant.slice(0, 20));
          setTotalLocation(res.data.data.resturant.length)
          setAllData(res.data.data)
          setDataLocation(res.data.data.resturant);
  
        })
        .catch(function (response) {
          //handle error
  
          console.log(response);
        });
    }
    setSearchLoading(false);

  };

  const handleChangePage = (event, newPage) => {
      console.log('callig new page')
    setPage(newPage);
    console.log(newPage, rowsPerPageShow);
    setSlicedData(
      dataLocation.slice(
        newPage * rowsPerPageShow,
        newPage * rowsPerPageShow + rowsPerPageShow
      )
    );
      if(newPage*rowsPerPageShow>= dataLocation.length-150)
      {
        locationList(currentCount)
      }
   
  };
  return (
    <div className={classes.backColor}>
      <Dialog
        fullWidth
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">
          {"Use Google's location service?"}
        </DialogTitle>
        <DialogContent>
          <div style={{ width: "80%", height: "50vh", overflow: "hidden" }}>
            {pitstop1 !== null ? (
              <Map XAxis={pitstop1.longitude} YAxis={pitstop1.latitude} />
            ) : null}
          </div>
        </DialogContent>
        <DialogActions>
          <Grid
            direction="column"
            item
            container
            alignItems="center"
            alignContent="center"
            className={classes.textalignment}
            style={{ marginTop: "20px" }}
          >
            <Button
              onClick={handleClose}
              variant="contained"
              className={classes.modalButton}
              component="span"
            >
              Close
            </Button>
          </Grid>
        </DialogActions>
      </Dialog>

      <div>
        <SideNavPage open={(e) => setOpenCloseSideBar(e)} />
      </div>
      <div className={classes.divHeight} style={{ marginLeft: marginLeft }}>
      {/* step={(e) => stepperSet(e)}  */}
        <Steeper step={(e) => stepperSet(e)} stepper={["Resturants", "Gas/Petrol Stations", "Bathrooms"]}/>
        <FadeIn transitionDuration="2000">
          <Grid
            style={{ marginTop: "20px", marginBottom: "50px" }}
            container
            justify="center"
            xl={12}
            lg={12}
            md={12}
            sm={12}
          >
            <form onSubmit={(e) => searchTheText(e)}>
              <Paper component="form" className={classes.root}>
                <InputBase
                  className={classes.input}
                  placeholder="Search Pitstop"
                  inputProps={{ "aria-label": "search google maps" }}
                  value={searchText}
                  onChange={(e) => {
                    setSearchText(e.target.value);
                  }}
                />

                <Divider className={classes.divider} orientation="vertical" />
                <IconButton
                  type="submit"
                  color="primary"
                  className={classes.iconButton}
                  aria-label="directions"
                  type="submit"
                >
                  {searchLoading ? (
                    <CircularProgress
                      size={20}
                      style={{ color: color.theme }}
                    />
                  ) : (
                    <SearchIcon style={{ color: color.theme }} />
                  )}
                </IconButton>
              </Paper>
            </form>
          </Grid>

          <Grid
            direction="row"
            item
            container
            alignItems="center"
            alignContent="center"
          >
            <Grid direction="column" xl={1} lg={1} md={1} sm={1}></Grid>
            <Grid direction="column" xl={10} lg={10} md={10} sm={10}>
              {" "}
              {/* <FadeIn setTimeout={1000}> */}
              <Grow
                in={true}
                style={{ transformOrigin: "0 0 0" }}
                {...(true ? { timeout: 1000 } : {})}
              >
                <ThemeProvider theme={themeTable}>
                  <ThemeProvider theme={themeTable}>
                    <MaterialTable
                      components={{
                        Pagination: (props) => (
                          <TablePagination
                            {...props}
                            rowsPerPageOptions={[5, 10, 20, 30, 50, 100]}
                            rowsPerPage={rowsPerPageShow}
                            count={totalLocation}
                            page={page}
                            onChangePage={handleChangePage}
                            onChangeRowsPerPage={(event) => {
                              props.onChangeRowsPerPage(event);
                              setRowsPerPageShow(event.target.value);
                              setSlicedData(
                                dataLocation.slice(
                                  page * event.target.value,
                                  page * event.target.value + event.target.value
                                )
                              );
                            }}
                          />
                        ),
                      }}
                      title={title}
                      columns={state}
                      data={slicedData}
                      options={{
                        search: false,
                        addRowPosition: "first",

                        pageSize: 20,
                        count: { totalLocation },

                        pageSizeOptions: [5, 10, 20, 30, 50, 75, 100],
                        toolbar: true,
                        paging: true,
                        actionsColumnIndex: -1,
                        rowStyle: (x) => {
                          if (x.tableData.id % 2) {
                            return { backgroundColor: "#f2f2f2" };
                          }
                        },
                        headerStyle: {
                          color: "rgb(39, 101, 114)",
                          fontSize: "120%",
                          fontWeight: "bold",
                        },
                      }}
                      icons={{
                        Add: forwardRef((props, ref) => (
                          <div style={{}}>
                            <StyledButton
                              size="small"
                              ref={ref}
                              {...props}
                              color="primary"
                              aria-label="add"
                            >
                              <AddIcon />
                            </StyledButton>
                          </div>
                        )),
                      }}
                      editable={{
                        // onRowAdd: (newData) =>
                        //   new Promise((resolve) => {
                        //     setTimeout(() => {
                        //       resolve();
                        //     //   setState((prevState) => {
                        //     //     const data = [...prevState.data];
                        //     //     data.push(newData);
                        //     //     // AddNewData(newData);
                        //     //     return { ...prevState, data };
                        //     //   });
                        //     }, 600);
                        //   }),
                        onRowUpdate: (newData, oldData) =>
                          new Promise((resolve) => {
                            setTimeout(() => {
                              resolve();
                              if (oldData) {
                                EditData(oldData, newData);
                              }
                            }, 600);
                          }),
                        onRowDelete: (oldData) =>
                          new Promise((resolve) => {
                            setTimeout(() => {
                              resolve();

                              DeleteData(oldData);
                            }, 600);
                          }),
                      }}
                    />
                  </ThemeProvider>
                </ThemeProvider>
              </Grow>
            </Grid>{" "}
            <Grid direction="column" xl={1} lg={1} md={1} sm={1}></Grid>
          </Grid>
        </FadeIn>
      </div>
    </div>
  );
}

Location.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(Location);
  